}

.Welcome-start-button {
  margin: 10px;
}

.Welcome-merry-christmas {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 4vmin);
  color: #ebf860;
  margin: 10px;
}

.Welcome-description {
  font-size: calc(10px + 2vmin);
