}
.Hint {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Hint-hint-item {
  margin-top: 10px;
  margin-bottom: 20px;
}

.Hint-hint-header {
  font-weight: 600;
  font-size: 1.1em;
}

