*/


.SecretInput {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.SecretInput-wrap {
  /* border: 1px solid rgba(0, 0, 0, 0.2); */
  display: inline-block;
  position: relative;
  display: flex;
}

.SecretInput-wrap-complete {
  color: #45ff24;
}

.SecretInput-wrap-incorrect {
  color: #993955;
}

.SecretInput-input {
  position: absolute;
  border: none;
  font-size: 32px;
  text-align: center;
  background-color: transparent;
  outline: none;
}

/* real width is 32px */
.SecretInput-display {
  border-bottom: 1px solid #9984D4;
  width: 28px;
  margin-left: 2px;
  margin-right: 2px;
  height: 58px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 32px;
  position: relative;
}

.SecretInput-display-complete {
  border-bottom: 1px solid #45ff24;
  /* 668586 */
  /* 1B998B */
}

.SecretInput-display-selected {
  border-bottom: 1px solid #ebf860;
}

.SecretInput-display-incorrect {
  border-bottom: 1px solid #993955;
}

.SecretInput-display:last-child {
  border-right: none;
}

.SecretInput-shadows {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  /* border-bottom: 1px solid #9984D4; */
  /* box-shadow: 0 0 0 4px rgba(58, 151, 212, 0.28); */
}
.Step {
  min-height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
 }

.Step-content {
