.Button {
  border-style: none;
  border-radius: 3px;
  background-color: #ebf860;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  color: white;
  font-size: calc(10px + 1vmin);
  cursor: pointer;
  outline: none;
}

.Button-disabled {
  background-color: #8f9fcf;
  cursor: not-allowed;
}