.Step {
  min-height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
 }

.Step-content {
  display: flex;
  flex-direction: column;
  /* align-self: center; */
  padding-top: 50px;
  padding-bottom: 50px;
  align-items: center;
}

.Step-start-button {
  margin-top: 20px;
}

.Step-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 4vmin);
  color: #ebf860;
  margin: 10px;
}

.Step-description {
  font-size: calc(10px + 2vmin);
  margin: 10px;
}

.Step-secret-input {
  margin: 30px;
}